import React, { useEffect, useState } from 'react';
// import { useIntl } from 'react-intl';
import { useDispatch, useSelector } from 'react-redux';
import Box from '@material-ui/core/Box';
import { Hidden } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import AppAnimate from '../../../@crema/core/AppAnimate';
import AppsContainer from '../../../@crema/core/AppsContainer';
import AppsHeader from '../../../@crema/core/AppsContainer/AppsHeader';
import AppsContent from '../../../@crema/core/AppsContainer/AppsContent';
import AppsPagination from '../../../@crema/core/AppsPagination';
import { AppState } from '../../../redux/store';
import ReportsTable from './ReportsTable';
import { onGetReportsData } from '../../../redux/actions/Users';

const AllUsers = () => {
    // const { messages } = useIntl();
    const dispatch = useDispatch();
    const { users, userCount } = useSelector<
        AppState,
        AppState['users']
    >(({ users }) => users);
    console.log(users);
    const [page, setPage] = useState<number>(0);
    const [search, setSearchQuery] = useState<string>('');
    const onPageChange = (
        event: React.ChangeEvent<unknown> | null,
        value: number,
    ) => {
        setPage(value);
    };
    useEffect(() => {
        dispatch(onGetReportsData(search, page));
    }, [dispatch, search, page]);

    const onSearchUser = (e: React.ChangeEvent<HTMLInputElement>) => {
        setSearchQuery(e.target.value);
        setPage(0);
    };

    return (
        <>
            <AppAnimate animation='transition.slideUpIn' delay={200}>
                <AppsContainer>
                    <AppsHeader>
                        <Box
                            display='flex'
                            flexDirection='row'
                            alignItems='center'
                            width={1}
                            justifyContent='space-between'>
                            <TextField
                                style={{ maxWidth: 150 }}
                                margin='dense'
                                id='user-name'
                                placeholder='Search'
                                type='search'
                                variant='outlined'
                                onChange={onSearchUser}
                            />
                            <Box display='flex' flexDirection='row' alignItems='center'>
                                <Hidden xsDown>
                                    <AppsPagination
                                        rowsPerPage={10}
                                        count={userCount}
                                        page={page}
                                        onPageChange={onPageChange}
                                    />
                                </Hidden>
                            </Box>
                        </Box>
                    </AppsHeader>

                    <AppsContent
                        style={{
                            paddingTop: 10,
                            paddingBottom: 10,
                        }}>
                        <ReportsTable tabledata={users} />
                    </AppsContent>

                    <Hidden smUp>
                        <AppsPagination
                            rowsPerPage={10}
                            count={userCount}
                            page={page}
                            onPageChange={onPageChange}
                        />
                    </Hidden>
                </AppsContainer>
            </AppAnimate>
        </>
    );
};

export default AllUsers;
