import React from 'react';
import TableCell from '@material-ui/core/TableCell';
import { makeStyles } from '@material-ui/core';
import TableRow from '@material-ui/core/TableRow';
import clsx from 'clsx';
import Box from '@material-ui/core/Box';


// import {TransactionData} from '../../../../../types/models/dashboards/Analytics';

import { CremaTheme } from '../../../../../types/AppContextPropsType';

export interface TransactionData {
  _id: string;
  title: string;
  text: string;
  fromTo: string;
  clicked: string;
  createdAt: string;
  totalclicked: string;
}

const useStyles = makeStyles((theme: CremaTheme) => ({
  tableCell: {
    fontSize: 13,
    padding: '12px 8px',
    '&:first-child': {
      paddingLeft: 20,
    },
    '&:last-child': {
      paddingRight: 20,
    },
  },
  anchar: {
    color: theme.palette.primary.main,
    borderBottom: `1px solid ${theme.palette.primary.main}`,
    display: 'inline-block',
  },
  badgeRoot: {
    padding: '3px 10px',
    borderRadius: 4,
    display: 'inline-block',
  },
}));

interface Props {
  data: TransactionData;
  num: string;
}

const TableItem: React.FC<Props> = ({ data, num }) => {
  const classes = useStyles();

  return (
    <TableRow key={data._id} className={clsx('item-hover')}>
      <TableCell component='th' scope='row' className={classes.tableCell}>
        <Box className={classes.anchar}>{num}</Box>
      </TableCell>
      <TableCell align='left' className={clsx(classes.tableCell)}>
        {data.fromTo}
      </TableCell>
      <TableCell align='left' className={classes.tableCell}>
        {data.title && data.title.slice(0, 30)}
      </TableCell>
      <TableCell align='left' className={classes.tableCell}>
        {data.createdAt && data.createdAt.slice(0, 19)}
      </TableCell>
      <TableCell align='left' className={classes.tableCell}>
        {`${data.clicked}/${data.totalclicked}`}
      </TableCell>
    </TableRow>
  );
};

export default TableItem;
